import noData from '@/components/noData'
import back from '@/components/back'
import { setInterval } from 'timers';

export default {
    components:{
        noData,
        back
    },
    data(){
        return{
            med: [],
            search: '',
            pres: [],
            user: this.$store.state.userInfo,
            page: 1,
            haveMore: true,
            showConfirm: false,
            showConfirm2: false,
            delPreId: null,
            delPreIdx: null,
            delMedId: null,
            delMedIdx: null,
            ListDetails: this.$store.state.checkCkooseList,
            removeMedI: null,
            removeMedIdx: null,
            showConfirm3: false,
            pageShow: false,
            medSearchList: null,
            inputFlag: false,
			defaultImg: 'this.src="' + require('../../assets/images/1.jpg') + '"',
			save:JSON.parse(localStorage.getItem('saveName')),
			checkIdshow:1,
            CensorateId:null,
            active:null,
            isShowXC:this.$store.state.isShowXC,
            activeTab:0,
            medList:[],
        }
    },
    computed:{
        totalPrice(){
            let p = 0
            if(this.ListDetails.length){
                this.ListDetails.forEach(v => {
                    p += (v.ItemPrice * v.ItemNum)
                })
            }
            return p.toFixed(2)
        }
    },
    watch: {
        activeTab(){
            this.page = 1
            this.haveMore = true
            this.pageShow = false
           this.pull(false,this.CensorateId)
        }
    },
    created(){
//         setInterval(() => {
//             this.$bridge.registerhandler('pharmacyChange', (data, responseCallback) => {
//                 this.$store.dispatch('pushpreNo', null)
//                 this.$store.dispatch('pushupList', [])
//                 this.ListDetails = []
//                 this.pull(false)
//                 responseCallback('接收到方法')
//             })
//         },10)
		this.$get('InspectionAPI/Censorate/QueryDefaultCensorate',{ //获取医生合作检查机构
		   DrId:this.user.Data.User.DoctorId,
		}).then(o => {
			this.CensorateId = o.CensorateId
			if(o.CensorateId){
				this.pull(false,o.CensorateId)
			}
		})
		if(this.save){
			if(this.$route.query.id){
				this.checkIdshow = 2
			}
		}
        
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        this.$store.dispatch('pushisUp',this.activeTab)
        window.onscroll = null
    },
    methods: {
		allMed(){
			this.$store.dispatch('pushsaveJCList',null)
			this.$router.push(`/inspect/all-medicine`)
		}, 
		toDetail(i){
			this.$router.push(`/inspect/checkIntroduction?CensorateItemId=${i.CensorateItemId}`)
		},
		BackTo(){
			this.$router.push('/inspect/checklist')
		},
        toUp(i){
			i.state = 'onP'
			this.ListDetails.push(i)
			this.ListDetails.forEach((v,k) => {
				if(!this.ListDetails[k].ItemNum){
					this.ListDetails[k].ItemNum = 1
				}
			})
			this.$store.dispatch('pushcheckCkooseList',this.ListDetails)
		},
		delToPre(i,idx){
		    this.removeI = i
		    this.removeIdx = idx
		    this.showConfirm3 = true
		},
		confirm3(){
		    this.ListDetails.forEach((v,k) => {
		        if(v.CensorateItemId == this.removeI.CensorateItemId){
		            this.ListDetails.splice(k,1)
		        }
		    })
		    this.med[this.removeIdx].state = null
		},
        searchByName(){
            if(this.inputFlag){
                return
            }
            if(this.search){
				this.$post('Prescription/Medication/GetMedicationByNameForOftenUsed',this.$pick({
					MedicationName: this.search,
					OrgId: this.user.Data.User.OrgId,
					DoctorId: this.user.Data.User.DoctorId,
					PageIndex: this.page,
					PageSize: 20
				})).then(o => {
					this.medSearchList = o.ReturnData
				})
			}
        },
        confirm(){
            this.$post('Prescription/Prescription/DelPrescriptionOftenUsed',{
                Id: this.delPreId
            }).then( o => {
                this.pres.splice(this.delPreIdx, 1)
            })
        },
        searchFor(){
			this.$get('InspectionAPI/Censorate/QueryDefaultCensorate',{ //获取医生合作检查机构
			   DrId: this.user.Data.User.DoctorId,
			}).then(o => {
				this.$store.dispatch('pushsaveJCList',null)
			    const censorateItemName = this.search
			    const censorateId = o.CensorateId
			    const censorateTypeId = 0
			    this.$store.dispatch('pushcId',censorateTypeId)
			    this.$store.dispatch('pushsearchText','')
			    this.$router.push(`/inspect/medicine-list?censorateItemName=${censorateItemName}&censorateId=${censorateId}&censorateTypeId=${censorateTypeId}`)
			})
        },
        toChangePharmacy(){
            this.$router.push(`/CooperativeOrganization?id=1`)
        },
        pull(h,v){ 
            if(this.haveMore){
                var _url = ''
                if(!this.activeTab){
                    _url = 'InspectionAPI/Censorate/QueryDrCensorateItemPage'//获取常用项目
                }else{
                    _url = 'InspectionAPI/Censorate/QueryDrCensorateUsePage' //获取常用检查单
                }
                this.$post(_url ,{
                    DrId:this.user.Data.User.DoctorId,
                    CensorateItemName: '',
                    CensorateId: 0,
                    PageIndex: this.page,
                    PageSize: 15
                }).then(o => {
                    this.pageShow = true
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    if(!this.activeTab){
                        const med = this.$store.state.checkCkooseList
                        if(med){
                            med.forEach(a => {
                                o.ReturnData.forEach(v => {
                                    if(v.CensorateItemId === a.CensorateItemId){
                                        v.state = 'onP'
                                    }
                                })
                            })
                        }
                        if (h){
                            o.ReturnData.forEach((v,k) => {
                                if(this.CensorateId != v.CensorateId){
                                    v.match = 1
                                }
                                this.med.push(v)
                            })
                        }
                        else {
                            o.ReturnData.forEach((v,k) => {
                                if(this.CensorateId != v.CensorateId){
                                    v.match = 1
                                }
                            })
                            this.med = o.ReturnData
                            console.log(this.med)
                        }
                    }else{
                        if (h){
                            o.ReturnData.forEach(v => {
                                this.medList.push(v)
                            })
                        }
                        else {
                            this.medList = o.ReturnData
                        }
                    }                   

                })
            }
            
        },
        useList(i){
            this.$get('InspectionAPI/Censorate/QueryDrCensorateUseAndDetail',{ //使用检查单(常用检查)
			   DrCensorateUseId: i.DrCensorateUseId,
			}).then(o => {
                let saveInputVal = {
                    ValidDaysRemark2: i.ClinicDesc,
                    Diagnoses: i.ClinicImpression,
                    ValidDaysRemark: '',
                }
                this.$store.dispatch('pushsaveInput',saveInputVal)
                this.$store.dispatch('pushcheckCkooseList',o.DrCensorateUseItems)
                if(this.CensorateId === o.CensorateId){
                    this.$router.push('/inspect/checklist')
                }else{
                    this.$router.push('/inspect/checklist?CensorateId='+ o.CensorateId +'&CensorateName='+ o.CensorateName)
                }
			})
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight
                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
						if(this.CensorateId){
							this.pull(true,this.CensorateId)
						}
                    }
                }, 100)
            }
        },
        toList(i){
            this.$store.dispatch('pushsearchText',i)
            if(this.$route.query.from == 'p'){
                this.$router.push(`/medicine-list?id=&from=ump`)
            }
            else if(this.$route.query.from == 'up'){
                this.$router.push(`/medicine-list?id=&from=upp`)
            }
            else{
                this.$router.push(`/medicine-list?id=&from=um`)
            }
        }
    }
}



